<div class="w-100"><div class="d-flex justify-content-center"><div class="card-wrap d-flex justify-content-center position-relative" v-for="item in data" :class="item.color" :style="`background-image: url('./${item.src}')`"><p class="ver-text card-title m-0">{{ item.label }}</p><p class="ver-text card-desc m-0" :class="item.color">{{ item.desc }}</p></div></div></div>